import { ofType } from 'redux-observable';
import { timeParse } from 'd3-time-format';
import { map, switchMap, mergeMap, catchError, concatMap } from 'rxjs/operators';
import { of, from as Rxfrom, merge } from 'rxjs';
import * as types from '../constants/actionTypesBook';
import { LOADING$ } from '../constants/actionTypesModal';
import {
    bookUpdate,
    recentTransactionUpdate,
    summaryUpdate,
    kLineGet$,
    kLineUpdate
} from '../actions/book';
import { updateLoading } from '../actions/modal';
import {
    bnHex,
    sellAmount,
    buyAmount,
    sellDisplayPrice,
    buyDisplayPrice,
    tenFill,
    bnTen
} from '../utils/format';

import OrderbookParser from '../utils/OrderbookParser';

const getBookEpic = (action$, state$) =>
{
    return action$
        .pipe(
            ofType(types.BOOK_GET$),
            switchMap(() =>
            {
                const { from, to } = state$.value.book;

                return Rxfrom(OrderbookParser.getAll(from.address, to.address))
            }),
            concatMap((orders) => {
                console.log('Send ', orders);
                const { from, to, tokenList } = state$.value.book;

                const fromToken = tokenList.find(tk => tk.address === from.address);
                const toToken = tokenList.find(tk => tk.address === to.address);

                const arrenge = (data) =>
                {
                    return data.sort((a, b) => (bnHex(a.price).gt(bnHex(b.price)) ? 1 : -1)) // 依照價格排序
                        .reduce((prev, curr) =>
                        {
                            // 將價格相同的數量合併
                            if (prev.length <= 0 || prev[prev.length - 1].price !== curr.price)
                            {
                                prev.push(curr);
                            }
                            else
                            {
                                prev[prev.length - 1].fromAmount = bnHex(prev[prev.length - 1].fromAmount).add(bnHex(curr.fromAmount)).toString(16);
                                prev[prev.length - 1].filled = bnHex(prev[prev.length - 1].filled).add(bnHex(curr.filled)).toString(16);
                            }

                            return prev;
                        }, [])
                        .map((v) =>
                        {
                            let book;
                            if (v.fromToken === from.address && v.toToken === to.address)
                            {
                                book = {
                                    from: fromToken.token,
                                    to: toToken.token,
                                    desc: `${fromToken.token}/${toToken.token}`,
                                    type: 'sell',
                                    timestamp: v.timestamp,
                                    price: v.price,
                                    displayPrice: sellDisplayPrice(v.price, from.decimal, to.decimal),
                                    amount: sellAmount(v.fromAmount, v.filled, from.decimal)
                                };
                            }
                            else if (v.fromToken === to.address && v.toToken === from.address)
                            {
                                book = {
                                    from: toToken.token,
                                    to: fromToken.token,
                                    desc: `${toToken.token}/${fromToken.token}`,
                                    type: 'buy',
                                    timestamp: v.timestamp,
                                    price: v.price,
                                    displayPrice: buyDisplayPrice(v.price, from.decimal, to.decimal),
                                    amount: buyAmount(v.fromAmount, v.filled, v.price, from.decimal, to.decimal)
                                };
                            }
                            book.value = Number(book.amount);
                            book.displayPrice = Number(book.displayPrice);
                            return book;
                        })
                        .filter(v => !!v);
                };

                let sell = orders.filter(d =>
                    (d.fromToken === from.address && d.toToken === to.address) &&
                    d !== undefined &&
                    bnHex(d.fromAmount).sub(bnHex(d.filled)).gt(bnTen((10 ** (from.decimal - 6)).toString())));

                let buy = orders.filter(d =>
                    (d.fromToken === to.address && d.toToken === from.address) &&
                    d !== undefined &&
                    bnHex(d.fromAmount).sub(bnHex(d.filled)).gt(bnTen((10 ** (from.decimal - 6)).toString())));

                sell = arrenge(sell).filter(d => d.type === 'sell')
                    .map((d, i, all) =>
                    {
                        if (i === 0)
                        {
                            return d;
                        }
                        else
                        {
                            d.value += all[i - 1].value;
                            return d;
                        }
                    });
                sell.reverse();

                buy = arrenge(buy).filter(d => d.type === 'buy')
                    .map((d, i, all) =>
                    {
                        if (i === 0)
                        {
                            return d;
                        }
                        else
                        {
                            d.value += all[i - 1].value;
                            return d;
                        }
                    });
                return of({ buy, sell });
            }),
            // debounceTime(3000),
            mergeMap(result => of(bookUpdate(result), updateLoading(false))),
            catchError((err, obs) => {
                console.error(err);
                return concat(
                    of(updateLoading(false)),
                    obs
                );
            })
        );
};

const getTransactionEpic = (action$, state$) =>
{
    return action$
        .pipe(
            ofType(types.RECENTTRANSACTION_GET$),
            switchMap((action) =>
            {
                const { from, to, tokenList } = state$.value.book;

                const fromToken = tokenList.find(tk => tk.address === from.address);
                const toToken = tokenList.find(tk => tk.address === to.address);

                const arr = Object.values(action.data || [])
                    .map((v) =>
                    {
                        let list;
                        if (v.fromToken === fromToken.address && v.toToken === toToken.address)
                        {
                            list = {
                                timestamp: v.timestamp,
                                price: v.price,
                                displayPrice: sellDisplayPrice(v.price, from.decimal, to.decimal),
                                amount: tenFill(bnHex(v.filled).toString(), -from.decimal),
                                type: 'sell',
                            };
                        }
                        else if (v.fromToken === toToken.address && v.toToken === fromToken.address)
                        {
                            list = {
                                timestamp: v.timestamp,
                                price: v.price,
                                displayPrice: buyDisplayPrice(v.price, from.decimal, to.decimal),
                                amount: tenFill(bnHex(v.filled)
                                    .mul(bnHex(v.price))
                                    .toString(), -(18 + from.decimal)),
                                type: 'buy'
                            };
                        }
                        return list;
                    })
                    .filter(v => !!v)
                    .sort((a, b) => b.timestamp - a.timestamp);

                return of(arr);
            }),
            mergeMap(result =>
                of(
                    recentTransactionUpdate(result.filter(v => new Date().getTime() - v.timestamp <= 86400 * 1000 * 30)),
                    // summaryGet$(result),
                    kLineGet$(result)
                )),
            catchError((err, obs) => obs)
        );
};

const getSummaryEpic = (action$) =>
{
    return action$
        .pipe(
            ofType(types.SUMMARY_GET$),
            map((action) =>
            {
                const now = new Date().getTime();
                const final = action.data.filter(d => now - d.timestamp <= 86400 * 1000);
                return summaryUpdate({
                    latest: final[final.length - 1].price || 0,
                    growth: final.length > 1 ? bnHex(final[final.length - 1].price).sub(bnHex(final[0].price)).div(bnHex(final[0].price)) : 0,
                    max: final.reduce((prev, curr) =>
                    {
                        return bnHex(curr.price).gt(bnHex(prev)) ? curr.price : prev;
                    }, 0),
                    min: final.length > 0 ? final.reduce((prev, curr) =>
                    {
                        if (!prev || bnHex(curr.price).lt(bnHex(prev)))
                        {
                            return curr.price;
                        }
                        else
                        {
                            return prev;
                        }
                    }, '') : 0,
                    amount: final.reduce((prev, curr) =>
                    {
                        return prev + (curr.amount);
                    }, 0)
                });
            }),
            catchError((err, obs) => obs)
        );
};

const getKLineEpic = (action$, state$) =>
{
    const parse = timeParse('%s');
    return action$
        .pipe(
            ofType(types.KLINE_GET$),
            switchMap((action) =>
            {
                if (action.data)
                {
                    return of(action.data.filter(d => d.type === 'sell'));
                }
                else
                {
                    return Rxfrom(window.dbObj.ref('priceBooks').once('value'))
                        .pipe(
                            map((snapshot) =>
                            {
                                const { from, to, tokenList } = state$.value.book;
                                const fromToken = tokenList.find(tk => tk.address === from.address);
                                const toToken = tokenList.find(tk => tk.address === to.address);
                                const data = Object.values(snapshot.val() || [])
                                    .map((v) =>
                                    {
                                        let list;
                                        if (v.fromToken === fromToken.address && v.toToken === toToken.address)
                                        {
                                            list = {
                                                timestamp: v.timestamp,
                                                price: v.price,
                                                displayPrice: sellDisplayPrice(v.price, fromToken.decimal, toToken.decimal),
                                                amount: tenFill(bnHex(v.filled).toString(), -from.decimal),
                                                type: 'sell',
                                            };
                                        }
                                        // else if (v.fromToken === toToken.address && v.toToken === fromToken.address)
                                        // {
                                        //     list = {
                                        //         timestamp: v.timestamp,
                                        //         price: v.price,
                                        //         displayPrice: buyDisplayPrice(v.price, toToken.decimal, fromToken.decimal),
                                        //         amount: tenFill(bnHex(v.filled)
                                        //             .mul(bnHex(v.price))
                                        //             .toString(), -(from.decimal + 18)),
                                        //         type: 'buy'
                                        //     };
                                        // }
                                        return list;
                                    })
                                    .filter(v => !!v)
                                    .sort((a, b) => b.timestamp - a.timestamp);
                                return data;
                            }),
                            catchError((err, obs) =>
                            {
                                console.warn(err);
                                return obs;
                            })
                        );
                }
            }),
            map((data) =>
            {
                const dataLength = 120;
                const timeunit = state$.value.book.timeunit;
                const now = new Date().getTime();
                const today = now - (now % timeunit);
                const startday = today - (timeunit * (dataLength - 1));

                const data5ma = new Array(dataLength).fill(0).map(() =>
                {
                    return { from: 0, to: 0 };
                });
                const data10ma = new Array(dataLength).fill(0).map(() =>
                {
                    return { from: 0, to: 0 };
                });
                const initialData = new Array(dataLength).fill(0).map((v, i) =>
                {
                    const d = {
                        '5ma': 0,
                        '10ma': 0,
                        high: 0,
                        low: 0,
                        open: 0,
                        close: 0,
                        amount: 0,
                        timestamp: startday + (timeunit * i)
                    };
                    return d;
                });

                const kLineData = data
                    .filter(v => v.timestamp > startday)
                    .reduce((prev, curr) =>
                    {
                        const rs = prev;
                        const i = Math.floor((curr.timestamp - startday) / timeunit);
                        const tmpFrom = Number(curr.amount);
                        const tmpTo = Number(curr.amount) * curr.displayPrice;

                        for (let j = 0; j < 5 && (i + j) < data5ma.length; j += 1)
                        {
                            data5ma[i + j].from += tmpFrom;
                            data5ma[i + j].to += tmpTo;
                        }

                        for (let k = 0; k < 10 && (i + k) < data10ma.length; k += 1)
                        {
                            data10ma[i + k].from += tmpFrom;
                            data10ma[i + k].to += tmpTo;
                        }

                        rs[i].low = rs[i].amount > 0 ? Math.min(rs[i].low, curr.displayPrice) : curr.displayPrice;
                        rs[i].high = Math.max(rs[i].high, curr.displayPrice);
                        rs[i].amount += Number(curr.amount);
                        if (rs[i].open === 0)
                        {
                            rs[i].open = curr.displayPrice;
                        }
                        rs[i].close = curr.displayPrice;
                        return rs;
                    }, initialData)
                    .map((v, i, arr) =>
                    {
                        const p = v;
                        const previ = i - 1 >= 0 ? i - 1 : 0;
                        p['5ma'] = (data5ma[i].to / data5ma[i].from) || arr[previ]['5ma'];
                        p['10ma'] = (data10ma[i].to / data10ma[i].from) || arr[previ]['10ma'];
                        p.timestamp = parse(p.timestamp / 1000);

                        if (p.amount === 0 && i > 0)
                        {
                            p.high = arr[i - 1].close;
                            p.low = arr[i - 1].close;
                            p.open = arr[i - 1].close;
                            p.close = arr[i - 1].close;
                        }
                        return p;
                    });

                return kLineUpdate(kLineData);
            }),
            catchError((err, obs) =>
            {
                console.warn(err);
                return obs;
            })
        );
};



export default [
    getBookEpic,
    getTransactionEpic,
    getSummaryEpic,
    getKLineEpic
];
