const ethEnabled = () => {
    if (window.ethereum) {
        window.web3 = new Web3(window.ethereum);
        window.ethereum.enable();
        return true;
    }
    return false;
}

export const setExtensionAddress = (callback) => {
    web3.eth.getAccounts((err, accs) => {
        if (typeof callback === 'function' && accs[0]) {
            web3.eth.defaultAccount = accs[0];
            if (accs.length === 0) return window.location.reload();
            callback(accs[0].replace('0x', ''));
        }
    });
}

export const startExtension = (login) => {
    window.ethereum.removeAllListeners();
    window.ethereum.on('accountsChanged', function (accounts) {
        if (typeof login === 'function' && accounts.length > 0) {
            login(accounts[0].replace('0x', ''));
        }
    });
    if (ethEnabled()) {
        web3 = window.web3;
        useExtension = true;
    }
    return;
    
}


