const sha3 = (str) => {
    return Web3.utils.sha3(str);
};


/**
 * 把字補齊到 l 的長度
 * @param  {[String]} str [String]
 * @param  {[type]} l   [number]
 * @return {[type]}     [String]
 */
function zero(str, l) {
    let rs = str;
    if (str.length < l) {
        rs = new Array(l - str.length).fill(0).join('') + rs;
    }
    return rs;
}

