/**
 * 產生數量16進位
 * @param  {[string]} amount [數量]
 * @param  {[string]} amount [數量]
 * @param  {[number]} decial [單位]
 * @return {[number]}
 */
function calAmount(amount, decimal) {
    const BN = Web3.utils.BN;
    if (/0x/.test(amount)) {
        return amount.replace('0x', '');
    }
    else {
        return new BN(tenFill(amount, decimal)).toString(16);
    }
}

/**
 * [取資料]
 * @param  {[type]} data [params => object]
 * @return {[type]}      [promise]
 */
function ecRequest(data, Url, hash) {
    return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();
        const url = Url || serverUrl;
        const method = 'POST';
        xhr.open(method, url);
        xhr.onreadystatechange = () => {
            if (xhr.readyState === XMLHttpRequest.DONE) {
                resolve(xhr.response);
            }
        };
        xhr.onerror = () => {
            reject({
                status: this.status,
                statusText: xhr.statusText
            });
        };

        const hashcash = echashcash(parser.pathname);
        xhr.setRequestHeader('Content-Type', 'application/json');
        xhr.setRequestHeader('hashcash', hash || hashcash);
        xhr.send(JSON.stringify(data));
    });
}

const checkReceipt = (param) => {
    const retry = 50;
    const wait = 7000;

    const func = useExtension ? () => new Promise((resolve, reject) => {
        web3.eth.getTransactionReceipt(param, (error, res) => {
            console.log('Error: '. error);
            console.log('Result: '. res);
            if (error) return resolve();
            resolve(res);
        })
    }) : () => new Promise((resolve, reject) => {
        const data = {
            jsonrpc: '2.0',
            method: 'eth_getTransactionReceipt',
            params: [param],
            id: 7
        };
        ecRequest(data).then((res) => {
            const result = JSON.parse(res);
            resolve(result);
        })
    })


    console.log('check receipt ******************', param);

    return new Array(retry).fill(0).reduce((prev) => {
        return prev.then((rs) => {
            if (rs) {
                return Promise.resolve(rs);
            }
            else {
                return func().then((res) => {
                    
                    if ( res === null || res.error || !(res.blockNumber > 0)) {
                        console.log(`re check receipt ${res}`);
                        return new Promise((resolve) => {
                            setTimeout(() => {
                                resolve();
                            }, wait);
                        });
                    }
                    else {
                        return Promise.resolve(res);
                    }
                });
            }
        });
    }, Promise.resolve());
};

/**
 * [取得 user addres nonce = > 錢包目前交易次數的流水號]
 * @param  {[type]} address [description]
 * @return {[type]}         [description]
 */
async function getNonce(address) {
    // const number = await web3.eth.getTransactionCount(`0x${address}`, 'pending');
    // return Promise.resolve(`0x${number.toString(16)}`);
    if (useExtension) {
        return new Promise((resolve, reject) => {
            web3.eth.getTransactionCount(`0x${address}`, (error, result) => {
                if (error) {
                    reject(error);
                } else {
                    console.log('Nonce: ', result)
                    resolve('0x' + bnTen(result).toString(16));
                }
            })
        })
    } else {
        const params = [`0x${address}`, 'latest'];
        const data = {
            jsonrpc: '2.0',
            method: 'eth_getTransactionCount',
            params,
            id: 7
        };
        return ecRequest(data).then((res) => {

            return Promise.resolve(JSON.parse(res).result);
        });
    }
}

/**
 * [取token最小單位]
 * @param  {[type]} token [object => { ...rest }]
 * @return {[type]}       [description]
 */
export function getDecimal(token) {
    if (token.address === ethAddress) {
        return Promise.resolve(18);
    }
    else {
        const data = `${sha3('decimals()').substr(0, 10)}000000000000000000000000`;
        if (useExtension) {

            return new Promise((resolve, reject) => {
                web3.eth.call({
                    to: token.address,
                    data
                }, (error, result) => {
                    if (error) {
                        reject(error);
                    } else {
                        resolve(Number(result));
                    }
                })
            })
        } else {
            const param = {
                jsonrpc: '2.0',
                method: 'eth_call',
                params: [{
                    to: token.address,
                    data,
                }, 'latest'],
                id: 7
            };
            return new Promise((resolve, reject) => {
                ecRequest(param).then((res) => {
                    try {
                        resolve(JSON.parse(res).result);
                    } catch (e) {
                        reject(e);
                    }
                })
            })

        }
    }
}

