export function getUserBalance(array, address) {
    const body = [];

    if (useExtension) {
        const promises = [];
        array.forEach((token) => {
            if (token.address === ethAddress) {
                promises.push(
                    new Promise((resolve, reject) => {
                        web3.eth.getBalance(address, (error, result) => {
                            if (error) {
                                reject(error);
                            } else {
                                const balanceHex = bnTen(result).toString(16);

                                resolve(balanceHex);
                            }
                        })
                    })
                )
            } else {
                promises.push(
                    new Promise((resolve, reject) => {
                        web3.eth.call({
                            to: token.address,
                            data: `${sha3('balanceOf(address)').substr(0, 10)}000000000000000000000000${address}`
                        }, (error, result) => {
                            if (error) {
                                reject(error);
                            } else {
                                resolve(result.replace('0x', ''))
                            }
                        })
                    })

                )
            }
        })
        return Promise.all(promises);
    } else {

        array.forEach((token) => {
            if (token.address === ethAddress) {
                const data = {
                    jsonrpc: '2.0',
                    method: 'eth_getBalance',
                    params: [`0x${address}`, 'latest'],
                    id: 7
                };
                body.push(data);
            }
            else {
                const data = {
                    jsonrpc: '2.0',
                    method: 'eth_call',
                    params: [{
                        data: `${sha3('balanceOf(address)').substr(0, 10)}000000000000000000000000${address}`,
                        to: token.address,
                    }, 'latest'],
                    id: 7
                };
                body.push(data);
            }
        });

        return new Promise((resolve, reject) => {
            return ecRequest(body).then((res) => {
                try {
                    const balances = JSON.parse(res).map(d => subHex(d.result));
                    resolve(balances);

                } catch (e) {
                    reject(e);
                }

            })
        })

    }
}


