/**
 * [取得 gas limit]
 * @param  {[type]} from  [ex: xpa address]
 * @param  {[type]} to    [ex: eth address]
 * @param  {[type]} value [要付的 eth]
 * @param  {[type]} data  [加密完的交易資料]
 * @param  {[type]} retry [重複次數]
 * @return {[type]}       [description]
 */
function getGasLimit(userAddress, to, value, data, retry) {
    const param = {
        from: userAddress,
        to,
        value,
        data
    }
    if (useExtension) {
        return new Promise((resolve, reject) => {
            web3.eth.estimateGas(param, (error, result) => {
                if (error) {
                    reject(error);
                } else {
                    console.log('Gas Limit', result);
                    resolve('0x' + bnTen(result).toString(16));
                }
            });
        });
    } else {
        let retryCounter = retry > 0 ? retry : 5;
        const waiting = 5000;

        const body = {
            jsonrpc: '2.0',
            method: 'eth_estimateGas',
            params: [param],
            id: 7
        };
        return new Array(retryCounter).fill(0).reduce((prev) => {
            return prev.then((rs) => {
                if (rs) {
                    return Promise.resolve(rs);
                }
                else {
                    return ecRequest(body).then((res) => {
                        const result = JSON.parse(res);
                        if (result.error) {
                            return new Promise((resolve) => {
                                setTimeout(() => {
                                    console.log('waiting', waiting, result.error);
                                    resolve();
                                }, waiting);
                            });
                        }
                        else {
                            console.log('success', res);
                            return Promise.resolve(JSON.parse(res).result);
                        }
                    });
                }
            });
        }, Promise.resolve());
    }

}

/**
 * [送出 raw 交易資料]
 * @param  {[type]} nonce    [description]
 * @param  {[type]} gasPrice [description]
 * @param  {[type]} from     [description]
 * @param  {[type]} to       [description]
 * @param  {[type]} key      [description]
 * @param  {[type]} data     [description]
 * @param  {[type]} value    [description]
 * @return {[type]}          [description]
 */
function sendRawTransaction(nonce, gasPrice, gasLimit, to, key, data, value) {
    console.log('nonce: ', nonce, 'gasPrice: ', gasPrice, 'gasLimit', gasLimit);

    if (useExtension) {
        return new Promise((resolve, reject) => {
            web3.eth.getAccounts((err, accs) => {
                if (err) {
                    reject(err);
                } else {
                    if (accs.length < 1) return reject('Acount not found');
                    web3.eth.sendTransaction({
                        from: accs[0],
                        data,
                        to,
                        value,
                        gasPrice: bnHex(gasPrice.replace('0x', '')).toString(),
                        gas: gasLimit,
                        nonce: Number(nonce)
                    }, (error, hash) => {
                        if (error) {
                            reject(error);
                        } else {
                            resolve(hash);
                        }
                    });
                }
            });
        });
    } else {
        const privateKey = ethereumjs.Buffer.Buffer.from(key, 'hex');
        const txParams = {
            nonce,
            gasPrice,
            gasLimit,
            to,
            value,
            data,
            chainId: 1
        };
        const tx = new ethereumjs.Tx(txParams);
        tx.sign(privateKey);
        const serializedTx = tx.serialize();
        const rawTx = `0x${serializedTx.toString('hex')}`;
        const params = [rawTx];
        const body = {
            jsonrpc: '2.0',
            method: 'eth_sendRawTransaction',
            params,
            id: 7
        };
        return ecRequest(body).then((res) => Promise.resolve(JSON.parse(res).result))
    }
}

export function calcFee(userAddress, to) {

    const data = `0xa028d749${zero(userAddress.replace('0x', ''), 64)}${zero(to.amount, 64)}${zero('1', 64)}`;

    if (useExtension) {
        return new Promise((resolve, reject) => {
            web3.eth.call({
                to: xpaExchange,
                data
            }, (error, res) => {
                if (error) {
                    reject(error);
                } else {
                    const result = res.substr(66, 64);
                    const fee = bnHex(result).toString()
                    resolve(Number(fee));
                }
            })
        })
    } else {
        const data = {
            jsonrpc: '2.0',
            method: 'eth_call',
            params: [{
                to: xpaExchange,
                data,
            }, 'latest'],
            id: 7
        };

        return ecRequest(data).then((res) => {
            const result = JSON.parse(res).result;
            const fee = result.substr(66, 64);
            return Number(`0x${fee}`);
        })
            .catch(console.log);
    }
}

/**
 * [取得Gas]
 * @param  {[type]} from        [description]
 * @param  {[type]} to          [description]
 * @param  {[type]} userAddress [description]
 * @param  {[type]} key         [私鑰]
 * @param  {[type]} arr         [能買賣到的單]
 * @return {[type]}             [description]
 */
export const estimateGas = (from, to, userAddress, key, agent) => {
    let nonce;
    let gasPrice = minGasPrice;
    let fromDecimal = 18;
    let toDecimal = 18;
    let data = '';
    let value = '0x0';

    if (from.address === ethAddress && to.address !== ethAddress) // from ETH to token
    {
        return getNonce(userAddress).then((n) => {
            nonce = n;
            return getGasPrice();
        })
            .then((g) => {
                gasPrice = Number(g) >= Number(minGasPrice) ? g : minGasPrice;
                return getDecimal(to);
            })
            .then((des) => {
                toDecimal = des;
                data = `${sha3('userTakeOrder(address,address,uint256,uint256,address)')
                    .substr(0, 10)}${zero('0', 64)}${zero(to.address.replace('0x', ''), 64)}${zero(from.price, 64)}${zero(from.amount, 64)}${agent ? zero('0', 64) : zero(agent.replace('0x', ''), 64)}`;
                value = `0x${from.amount}`;
                return getGasLimit(`0x${userAddress}`, xpaExchange, value, data, 50);
            })
            .then((num) => {
                const gasLimit = num;

                return {
                    nonce,
                    gasPrice,
                    gasLimit,
                    fromDecimal,
                    toDecimal,
                    from,
                    to,
                    key,
                    data,
                    value,
                    agent
                };
            })
            .catch((err) => {
                console.log(err);
                return false;
            });
    }
    else {
        return getNonce(userAddress).then((n) => {
            nonce = n;
            return getGasPrice();
        })
            .then((g) => {
                gasPrice = Number(g) >= Number(minGasPrice) ? g : minGasPrice;
                return getDecimal(from);
            })
            .then((des) => {
                fromDecimal = Number(des);
                return getDecimal(to);
            })
            .then((des) => {
                toDecimal = Number(des);
                return getAllowance(`0x${userAddress}`, from, xpaExchange);
            })
            .then((ap) => {
                console.log('allowance', ap);
                data = `${sha3('userTakeOrder(address,address,uint256,uint256,address)')
                    .substr(0, 10)}${zero(from.address.replace('0x', ''), 64)}${zero(to.address.replace('0x', ''), 64)}${zero(from.price, 64)}${zero(from.amount, 64)}${agent ? '' : zero(agent.replace('0x', ''), 64)}`;
                if (Number(ap) === 0) {
                    return getGasLimit(`0x${userAddress}`, xpaExchange, value, data, 50);
                }
                else {
                    const approve = `${sha3('approve(address,uint256)')
                        .substr(0, 10)}${zero(xpaExchange.replace('0x', ''), 64)}${zero('0', 64)}`;

                    return sendRawTransaction(nonce, gasPrice, approveGasLimit, from.address, key, approve, '0x0').then((res) => {
                        return checkReceipt(res);
                    })
                        .then(() => {
                            return getGasLimit(`0x${userAddress}`, from.address, '0x0', data, 50);
                        });
                }
            })
            .then((num) => {
                const gasLimit = num;
                return {
                    nonce,
                    gasPrice,
                    gasLimit,
                    from,
                    to,
                    key,
                    data,
                    value,
                    fromDecimal,
                    toDecimal,
                    agent,
                    userAddress
                };
            })
            .catch((err) => {
                console.log(err);
                return false;
            });
    }
};

