/**
 * [執行掛單]
 * @param  {[type]} from        [description]
 * @param  {[type]} to          [description]
 * @param  {[type]} userAddress [description]
 * @param  {[type]} key         [私鑰]
 * @param  {[type]} arr         [能買賣到的單]
 * @return {[type]}             [description]
 */
export async function transaction(tx) {
    console.log(tx);
    let _receipt;
    if (tx.from.address === ethAddress && tx.to.address !== ethAddress) // from ETH to token
    {
        return sendRawTransaction(tx.nonce, tx.gasPrice, tx.gasLimit, xpaExchange, tx.key, tx.data, tx.value)
            .then((receipt) => {
                _receipt = receipt;
                return checkReceipt(receipt);
            })
            .then(() => {
                return _receipt;
            })
            .catch(() => {
                return false;
            });
    }
    else {
        const approve = `${sha3('approve(address,uint256)')
            .substr(0, 10)}${zero(xpaExchange.replace('0x', ''), 64)}${zero(tx.from.amount, 64)}`;
        console.log('data', approve);
        return sendRawTransaction(tx.nonce, tx.gasPrice, approveGasLimit, tx.from.address, tx.key, approve, '0x0')
            .then((res) => {
                return checkReceipt(res);
            }).then(() => {
                tx.nonce = `0x${(parseInt(tx.nonce, 16) + 1).toString(16)}`;
                return getGasLimit(`0x${tx.userAddress}`, xpaExchange, '0x0', tx.data, 50);
            })
            .then((res) => {
                const limit = res;
                console.log('new limit', limit);
                return sendRawTransaction(tx.nonce, tx.gasPrice, limit, xpaExchange, tx.key, tx.data, '0x0');
            })
            .then((receipt) => {
                _receipt = receipt;
                return checkReceipt(receipt);
            })
            .then(() => {
                return _receipt;
            })
            .catch((err) => {
                console.log(err);
                return false;
            });
    }
}


