    if (tx.from.address === ethAddress && tx.to.address !== ethAddress) {
        return Promise.resolve({ raw: `iban:${eciban.toIBAN(xpaExchange)}?data=${tx.data}&value=${Number(tenFill(bnHex(tx.from.amount).toString(), -18))}&amount=${Number(tenFill(bnHex(tx.from.amount).toString(), -18))}` });
    }
