export function estimateCancel(from, to, userAddress, key, agent) {
    let gasPrice = minGasPrice;
    let data;
    data = `${sha3('userCancelOrder(address,address,uint256,uint256,address)')
        .substr(0, 10)}${zero(from.address.replace('0x', ''), 64)}${zero(to.address.replace('0x', ''), 64)}${zero(from.price, 64)}${zero(from.amount, 64)}${agent ? zero('0', 64) : zero(agent.replace('0x', ''), 64)}`;

    return getGasPrice()
        .then((g) => {
            gasPrice = Number(g) >= Number(minGasPrice) ? g : minGasPrice;
            return getGasLimit(`0x${userAddress}`, xpaExchange, '0x0', data, 50);
        })
        .then((res) => {
            return {
                gasLimit: res,
                key,
                userAddress,
                data,
                gasPrice
            };
        })
        .catch((err) => {
            console.log(err);
        });
}

