export function getMortgagePrice() {
    const promises = [];
    const result = {};
    setWeb3Provider();

    const call = (key) => {
        return new Promise((resolve, rejex) => {
            return web3.eth.call({
                to: XPAAsset,
                data: `${Web3.utils.sha3('getPrice(address)').substr(0, 10)}${zero(cash[key].replace('0x', ''), 64)}`
            }, 'latest', (error, result) => {
                if (error) {
                    reject(error);
                } else {
                    resolve(result)
                }
            });
        })

    };

    Object.keys(cash).map((key) => {
        if (key === 'XPA') {
            return null;
        }
        else {
            result[key] = '';
            return promises.push(call(key));
        }
    });

    return Promise.all(promises).then((res) => {
        Object.keys(result).forEach((d, i) => {
            result[d] = subHex(res[i]);
        });

        return result;
    });
}

