export function getAmount(userAddress, type) {
    let method;
    const promises = [];
    const result = {};
    setWeb3Provider();

    if (type === 'usable') {
        method = 'getUsableAmount';
    }
    else if (type === 'loan') {
        method = 'getLoanAmount';
    }
    else {
        method = 'getRemainingAmount';
    }

    const call = (key) => {
        return new Promise((resolve, reject) => {
            web3.eth.call({
                to: XPAAsset,
                data: `${sha3(`${method}(address,address)`).substr(0, 10)}${zero(userAddress.replace('0x', ''), 64)}${zero(cash[key].replace('0x', ''), 64)}`
            }, 'latest', (error, result) => {
                if (error) {
                    reject(error);
                } else {
                    resolve(result);
                }
            });
        })
    };

    Object.keys(cash).map((key) => {
        if (key === 'XPA') {
            return null;
        }
        else {
            result[key] = '';
            return promises.push(call(key));
        }
    });

    return Promise.all(promises).then((res) => {
        Object.keys(result).forEach((d, i) => {
            result[d] = subHex(res[i]);
        });

        return result;
    });
}

