export function estimateMortgage(from, to, userAddress, key, agent) {
    let fromDecimal = 18;
    let nonce;
    let gasPrice = minGasPrice;
    let value = '0x0';
    let data;
    const gasLimit = '0x13880';

    return getNonce(userAddress).then((n) => {
        nonce = n;
        return getGasPrice();
    }).then((g) => {
        gasPrice = Number(g) >= Number(minGasPrice) ? g : minGasPrice;
        console.log('gasPrice', gasPrice);
        return getAllowance(`0x${userAddress}`, from, XPAAsset);
    })
        .then((ap) => {
            console.log('allowance is', JSON.parse(ap).result);
            data = `${sha3('mortgage(address)')
                .substr(0, 10)}${agent ? zero('0', 64) : zero(agent.replace('0x', ''), 64)}`;
            if (Number(JSON.parse(ap).result) === 0) {
                return Promise.resolve();
            }
            else {
                const approve = `${sha3('approve(address,uint256)')
                    .substr(0, 10)}${zero(XPAAsset.replace('0x', ''), 64)}${zero('0', 64)}`;
                console.log('approve 0');
                return sendRawTransaction(nonce, gasPrice, approveGasLimit, from.address, key, approve, '0x0').then((res) => {
                    return checkReceipt(res);
                })
                    .then(() => {
                        return Promise.resolve();
                    });
            }
        })
        .then(() => {
            return {
                nonce,
                gasPrice,
                gasLimit,
                from,
                to,
                key,
                data,
                value,
                fromDecimal,
                userAddress,
            };
        })
        .catch((err) => {
            console.log(err);
            return false;
        });
}

