export async function estimateAct(from, userAddress, key, method, agent) {
    let nonce;
    let gasPrice = minGasPrice;
    console.log(from, method);
    let data;
    if (from.cash === 'XPA') {
        data = `${sha3('withdrawXPA(uint256,address)').substr(0, 10)}${zero(calAmount(from.amount, 18), 64)}${agent ? zero('0', 64) : zero(agent.replace('0x', ''), 64)}`;
    }
    else {
        data = `${sha3(`${method === 'withdraw' ? 'withdraw' : 'repayment'}(address,uint256,address)`).substr(0, 10)}${zero(cash[from.cash].replace('0x', ''), 64)}${zero(calAmount(from.amount, 18), 64)}${agent ? zero('0', 64) : zero(agent.replace('0x', ''), 64)}`;
    }
    let fee = 0;
    if (method === 'withdraw') {
        fee = await withdrawFee();
    }

    return getNonce(userAddress).then((n) => {
        nonce = n;
        return getGasPrice();
    })
        .then((g) => {
            gasPrice = Number(g) >= Number(minGasPrice) ? g : minGasPrice;
            return getGasLimit(`0x${userAddress}`, XPAAsset, '0x0', data, 50);
        })
        .then((num) => {
            const gasLimit = num;

            return {
                nonce,
                gasPrice,
                gasLimit,
                from,
                key,
                data,
                userAddress,
                approve: method === 'withdraw' ? false : true,
                fee
            };
        })
        .catch((err) => {
            console.log(err);
            return false;
        });
}

export function doAct(tx) {
    let _receipt;

    if (tx.approve) {
        const approve = `${sha3('approve(address,uint256)').substr(0, 10)}${zero(XPAAsset.replace('0x', ''), 64)}${zero(calAmount(tx.from.amount, 18), 64)}`;
        return sendRawTransaction(tx.nonce, tx.gasPrice, approveGasLimit, cash[tx.from.cash], tx.key, approve, '0x0')
            .then((res) => {
                return checkReceipt(res);
            }).then(() => {
                tx.nonce = `0x${(parseInt(tx.nonce, 16) + 1).toString(16)}`;
                return getGasLimit(`0x${tx.userAddress}`, XPAAsset, '0x0', tx.data, 50);
            })
            .then((res) => {
                const limit = res;
                return sendRawTransaction(tx.nonce, tx.gasPrice, limit, XPAAsset, tx.key, tx.data, '0x0');
            })
            .then((receipt) => {
                _receipt = receipt;
                return checkReceipt(receipt);
            })
            .then(() => {
                return _receipt;
            })
            .catch((err) => {
                console.log(err);
                return false;
            });
    }
    else {
        return sendRawTransaction(tx.nonce, tx.gasPrice, tx.gasLimit, XPAAsset, tx.key, tx.data, '0x0')
            .then((receipt) => {
                _receipt = receipt;
                return checkReceipt(receipt);
            })
            .then(() => {
                return _receipt;
            })
            .catch((err) => {
                console.log(err);
                return false;
            });
    }
}

