
// limit number length smaller then l
import Web3 from 'web3';

const BN = Web3.utils.BN;

function accMul(arg1, arg2)
{
    let m = 0;
    const s1 = arg1.toString();
    const s2 = arg2.toString();
    try
    {
        m += s1.split('.')[1].length;
    }
    catch (e)
    {}
    try
    {
        m += s2.split('.')[1].length;
    }
    catch (e)
    {}

    return (Number(s1.replace('.', '')) * Number(s2.replace('.', ''))) / (10 ** m);
}

export function formatNumber(l, num, max)
{
    const float = (`${num}`).split('.')[1];
    const int = (`${num}`).split('.')[0];
    const total = `${num}`.length - 1;
    const maxFloatLen = max || 9;
    if (!num) return num;
    if (typeof num === 'string') num = Number(num);
    try
    {
        if (total < l && (l - int.length) >= 0)
        {
            // const de = l - int.length;
            const de = l;
            return parseFloat((Math.floor(accMul(num, (10 ** de))) / (10 ** de)).toFixed(de));
        }
        else if (total < l && (l - total >= 0))
        {
            // const de = l - total;
            const de = l;
            return parseFloat((Math.floor(accMul(num, (10 ** de))) / (10 ** de)).toFixed(de));
        }
        else
        {
            if (total > l && !float)
            {
                return num.toFixed(0);
            }
            const de = l - int.length > 0 ? l - int.length : 0;
            if (float && float.length >= maxFloatLen && float.length > l)
            {
                return parseFloat(num.toFixed(de));
            }
            return parseFloat((Math.floor(accMul(num, (10 ** de))) / (10 ** de)).toFixed(de));
        }
    }
    catch (err)
    {
        console.warn(err);
        return num;
    }

    // if (!num) return num;
    // try
    // {
    //     return parseFloat((Math.floor(accMul(num, (10 ** l))) / (10 ** l)).toFixed(l));
    // }
    // catch (err)
    // {
    //     // console.warn(err);
    //     return num;
    // }
}

export function formatTime(timestamp, type)
{
    const date = new Date(timestamp);
    const d = [
        date.getFullYear(),
        date.getMonth() + 1,
        date.getDate(),
        date.getHours(),
        date.getMinutes(),
        date.getSeconds(),
    ];
    if (String(d[4]).length < 2)
    {
        d[4] = `0${d[4]}`;
    }
    if (String(d[5]).length < 2)
    {
        d[5] = `0${d[5]}`;
    }

    switch (type)
    {
        case 'time':
            return `${d[3]}:${d[4]}:${d[5]}`;
        case 'date':
            return `${d[1]}-${d[2]} ${d[3]}:${d[4]}:${d[5]}`;
        default:
            return `${d[0]}-${d[1]}-${d[2]} ${d[3]}:${d[4]}:${d[5]}`;
    }
}

const isFloat = (n) =>
{
    return Number(n) === n && n % 1 !== 0;
};

export function times(num1, num2)
{
    let dot1 = String(num1).split('.')[1];
    let dot2 = String(num2).split('.')[1];
    const Num1 = Number(num1);
    const Num2 = Number(num2);

    if (dot1)
    {
        dot1 = dot1.length <= 6 ? 6 : 10;
    }
    else
    {
        dot1 = 0;
    }

    if (dot2)
    {
        dot2 = dot2.length <= 6 ? 6 : 10;
    }
    else
    {
        dot2 = 0;
    }

    const fixedFloor = (num) => {
        return parseFloat(num.toFixed(9));
    };

    if (isFloat(Num1) && isFloat(Num2))
    {
        return fixedFloor(((Num1 * (10 ** dot1)) * (Num2 * (10 ** dot2))) / ((10 ** dot1) * (10 ** dot2)));
    }
    else if (!isFloat(Num1) && isFloat(Num2))
    {
        return fixedFloor((Num1 * (Num2 * (10 ** dot2))) / (10 ** dot2));
    }
    else if (isFloat(Num1) && !isFloat(Num2))
    {
        return fixedFloor(((Num1 * (10 ** dot1)) * Num2) / (10 ** dot1));
    }
    else
    {
        return Num1 * Num2;
    }
}

// 科學符號轉字串 Scientific notation To String
export function scnToStr(x)
{
    if (Math.abs(x) < 1.0)
    {
        let e = parseInt(x.toString().split('e-')[1], 10);
        if (e)
        {
            x *= (10 ** (e - 1));
            x = `0.${(new Array(e)).join('0')}${x.toString().substring(2)}`;
        }
    }
    else
    {
        let e = parseInt(x.toString().split('+')[1], 10);
        if (e > 20)
        {
            e -= 20;
            x /= (10 ** e);
            x += (new Array(15)).join('0');
        }
    }
    return String(x);
}

export const subHex = (input) =>
{
    let index = 0;
    if (!input || input.length < 1) return input;
    let result = input.replace('0x', '');
    for (let i = 0; i < result.length; i += 1)
    {
        if (result[i] === '0')
        {
            index = i + 1;
        }
        else
        {
            break;
        }
    }

    result = result.substr(index, result.length);
    return result ? result : '0';
};

export const bnHex = (str) =>
{
    if (!str || str === '-') return new BN('0', 16);
    return new BN(str, 16);
};

export const bnTen = (str) =>
{
    if (!str || str === '-') return new BN('0', 10);
    return new BN(str, 10);
};

export const tenFill = (n, idx) =>
{
    if (typeof n === 'number')
    {
        n = n.toString();
    }
    const tmpArr = n.split('.');
    const abs = Math.abs(idx);

    if (tmpArr.length < 2)
    {
        tmpArr.push('');
    }

    const l1 = tmpArr[0].length;
    const l2 = tmpArr[1].length;
    if (idx > 0)
    {
        tmpArr[0] += tmpArr[1].substr(0, abs);
        tmpArr[1] = tmpArr[1].substr(abs);
        if (abs >= l2)
        {
            tmpArr[0] += new Array(abs - l2).fill(0).join('');
        }
    }
    else if (idx < 0)
    {
        tmpArr[1] = tmpArr[0].substr(idx) + tmpArr[1];
        tmpArr[0] = tmpArr[0].substr(0, tmpArr[0].length + idx);
        if (abs >= l1)
        {
            tmpArr[1] = new Array(abs - l1).fill(0).join('') + tmpArr[1];
        }
    }

    if (tmpArr[0].length === 0)
    {
        tmpArr[0] = '0';
    }

    let index;
    for (index = tmpArr[1].length; index > 0; index -= 1)
    {
        if (tmpArr[1][index - 1] !== '0')
        {
            break;
        }
    }
    tmpArr[1] = tmpArr[1].substr(0, index);
    return tmpArr[1].length > 0 ? tmpArr.join('.') : tmpArr[0];
};

export const sellAmount = (fAmount, fFill, fDecimal) =>
{
    return tenFill(bnHex(fAmount)
        .sub(bnHex(fFill))
        .toString(), -fDecimal);
};

export const sellDisplayPrice = (price, fDecimal, tDecimal) =>
{
    return tenFill(bnHex(price).toString(), fDecimal - tDecimal - 18);
};

export const buyAmount = (fAmount, fill, price, fDecimal) =>
{
    return tenFill(bnHex(fAmount)
        .sub(bnHex(fill))
        .mul(bnHex(price))
        .toString(), -fDecimal - 18);
};

export const buyDisplayPrice = (price, fDecimal, tDecimal) =>
{
    return tenFill(bnTen(tenFill(1, 36))
        .div(bnHex(price))
        .toString(), fDecimal - tDecimal - 18);
};

