/* eslint-disable no-undef */
import update from 'immutability-helper';
import * as types from '../constants/actionTypeMortgage';

const cash = { ...finalConfig.assetToken };
Object.keys(cash).forEach((k) =>
{
    cash[k] = 0;
});

const initialState = {
    mortgageRate: 0,
    equivalentAmount: 0,
    usable: { ...cash },
    loan: { ...cash },
    remaining: { ...cash },
    price: { ...cash },
    closingLine: 0,
    usableXPA: 0
};

export default function mortgage(state = initialState, action = {})
{
    switch (action.type)
    {
        case types.CHANGE_MORTGAGE_RATE:
            return update(state, {
                mortgageRate: { $set: action.data }
            });
        case types.CHANGE_EQUIVALENT_AMOUNT:
            return update(state, {
                equivalentAmount: { $set: action.data }
            });
        case types.CHANGE_USABLE:
            return update(state, {
                usable: { $set: action.data }
            });
        case types.CHANGE_LOAN:
            return update(state, {
                loan: { $set: action.data }
            });
        case types.CHANGE_REMAINING:
            return update(state, {
                remaining: { $set: action.data }
            });
        // case types.CHANGE_CLOSING_LINE:
        //     return update(state, {
        //         closingLine: { $set: action.data }
        //     });
        case types.CHANGE_USABLE_XPA:
            return update(state, {
                usableXPA: { $set: action.data }
            });
        case types.CHANGE_MORTGAGE_PRICE:
            return update(state, {
                price: { $set: action.data }
            });
        case types.MORTGAGE_LOGOUT:
            return update(state, {
                mortgageRate: { $set: '-' },
                equivalentAmount: { $set: 0 },
                usable: { $set: cash },
                loan: { $set: cash },
                remaining: { $set: cash },
                closingLine: { $set: 0 },
                usableXPA: { $set: 0 }
            });
        default:
            return state;
    }
}

