import update from 'immutability-helper';
import * as types from '../constants/actionTypesUser';

const initialState = {
    user: {},
    account: [],
    myBooks: [],
    myTransaction: {
        price: '',
        amount: '',
        total: '-',
        able: false,
        act: 'buy'
    },
    keep: false,
    myHistory: []
};

export default function user(state = initialState, action = {})
{
    switch (action.type)
    {
        case types.USER_UPDATE:
            return update(state, {
                user: { $set: action.data }
            });
        case types.MYBOOKS_UPDATE:
            return update(state, {
                myBooks: { $set: action.data }
            });
        case types.MYBOOKS_ADD:
            return update(state, {
                myBooks: { $unshift: [action.data] }
            });
        case types.MYBOOKS_CANCEL:
            return update(state, {
                myBooks: {
                    [action.data]: {
                        status: { $set: 'cancel' }
                    }
                }
            });
        case types.MYBOOKS_CHANGE:
            return update(state, {
                myBooks: {
                    [action.data]: {
                        cancel: { $set: true }
                    }
                }
            });
        // case types.GET_BALANCE:
        //     return update(state, {
        //         account: {
        //             [action.data.index]: {
        //                 usable: { $set: action.data.value },
        //             }
        //         }
        //     });
        case types.MYTRANSACTION_CHANGE:
            return update(state, {
                myTransaction: {
                    $set: action.data
                }
            });
        case types.MYTRANSACTION_UPDATE:
            return update(state, {
                myTransaction: {
                    [action.data.type]: { $set: action.data.value }
                }
            });
        case types.ACCOUNT_UPDATE:
            return update(state, {
                account: { $set: action.data }
            });
        case types.KEEP:
            return update(state, {
                keep: { $set: true }
            });

        case types.DOWNLOADED:
            return update(state, {
                keep: { $set: false }
            });
        case types.HISTORY_UPDATE:
            return update(state, {
                myHistory: { $set: action.data }
            });
        default:
            return state;
    }
}
