import React from 'react';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { bnHex, tenFill, formatNumber } from '../../utils/format';
import './tokenList.scss';

class TokenList extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            filter: '',
            // eslint-disable-next-line no-undef
            base: { token: 'ETH', address: finalConfig.ethAddress, decimal: 18 },
            favorite: JSON.parse(localStorage.getItem('favorite')) || []
        };

        this.favoriteToggle = this.favoriteToggle.bind(this);
        this.filter = this.filter.bind(this);
        this.handleClick = this.handleClick.bind(this);
    }

    changeToken(t)
    {
        let basic = {};

        if (t === 'FAV')
        {
            basic = { token: 'FAV', address: '' };
        }
        else
        {
            basic = this.props.tokenList.find(d => d.token === t && d.basic === true);
            // console.log(t, basic);
        }
        this.setState(update(this.state, {
            base: { $set: basic },
        }));
    }

    favoriteToggle(item, e)
    {
        e.stopPropagation();

        // 沒存
        if (this.state.favorite.indexOf(item.id) === -1)
        {
            this.setState(update(this.state, {
                favorite: { $push: [item.id] }
            }), () => {
                localStorage.setItem('favorite', JSON.stringify(this.state.favorite));
            });
        }
        else // 有存, 取消
        {
            let addressId = this.state.favorite.filter((val) => {
                return val !== item.id;
            });

            this.setState(update(this.state, {
                favorite: { $set: addressId }
            }), () => {
                localStorage.setItem('favorite', JSON.stringify(this.state.favorite));
            });
        }
    }

    filter(e)
    {
        this.setState(update(this.state, {
            filter: { $set: e.target.value }
        }));
    }

    handleClick()
    {
        if (this.state.filter !== '')
        {
            this.setState(update(this.state, {
                filter: { $set: '' }
            }));
        }
    }

    renderMenu()
    {
        const { tradeList, select, tokenList, t } = this.props;
        const { base, filter, favorite } = this.state;

        let listArray = [];
        let list;
        let searchStyle = '';

        if (!tradeList[base.token] && base.token !== 'FAV')
        {
            return null;
        }

        // favoriteData
        let favoriteData = [];
        if (favorite.length > 0)
        {
            for (let key in tradeList)
            {
                for (let val of tradeList[key])
                {
                    if (favorite.indexOf(val.id) !== -1)
                    {
                        favoriteData.push(val);
                    }
                }
            }
        }

        // filter
        if (filter !== '')
        {
            if (base.token !== 'FAV')
            {
                listArray = tradeList[base.token].filter(l => l.token.toLowerCase().indexOf(filter.toLowerCase()) > -1);
            }
            else
            {
                // 我的最愛
                listArray = favoriteData.filter(l => l.token.toLowerCase().indexOf(filter.toLowerCase()) > -1);
            }
        }
        else
        {
            listArray = (base.token !== 'FAV') ? tradeList[base.token] : favoriteData;
        }

        if (listArray.length > 0)
        {
            list = listArray.map((l, i) =>
            {
                const isFavorite = (this.state.favorite.indexOf(l.id) !== -1) ? 'checked' : '';
                const fav = tokenList.find(d => d.token === l.base && d.basic === true);
                // if(!fa)
                // {
                //     localStorage.clear
                // }
                const from = tokenList.find(d => d.address === l.address) || { decimal: 18 };
                const to = tokenList.find(d => d.address === base.address) || { decimal: 18 };
                const baseAddress = (base.token === 'FAV' && fav) ? fav.address : base.address;
                return (
                    <li key={l.id} onClick={() => { select({ type: l.base, baseAddress, id: l.id, decimal: 18 }); }}>
                        <div>
                            <i onClick={(e) => { this.favoriteToggle(l, e); }} className={`fa fa-star ${isFavorite}`} aria-hidden="true" />
                        </div>
                        <div className="desc"><span>{l.token}</span>/{l.base}</div>
                        <div className="price">{formatNumber(8, tenFill(bnHex(l.latest).toString(), from.decimal - 18 - to.decimal))}</div>
                        <div className={String(l.growth).indexOf('-') > -1 ? 'ratio red' : 'ratio green'}>{l.growth}%</div>
                    </li>
                );
            });
        }
        else
        {
            // list = <div className="no-result">{t('header.noResult')}</div>;
            list = null;
            if (filter !== '')
            {
                searchStyle = 'no-result';
            }
        }

        const type = Object.keys(tradeList).map((k) =>
        {
            return (
                <div key={k} onClick={() => this.changeToken(`${k}`)} className={this.state.base.token === `${k}` ? 'active' : ''}>{k}</div>
            );
        });

        return (
            <div>
                <div className="search">
                    <input
                        type="text"
                        className={searchStyle === 'no-result' ? searchStyle : ''}
                        onChange={this.filter}
                        value={filter}
                    />
                    <img src={`/img/icon/${filter === '' ? 'ic-search.svg' : 'ic-del.svg'}`} alt="search" onClick={this.handleClick} />
                </div>
                <div className="option">
                    <div onClick={() => this.changeToken('FAV')} className={base.token === 'FAV' ? 'active' : ''}>{this.props.t('table.star')}</div>
                    {type}
                </div>
                <li className="list-title">
                    <div />
                    <div />
                    <div>{t('header.lastPrice')}</div>
                    <div>{t('header.change')}</div>
                </li>
                <ul className="list">
                    {list}
                </ul>
            </div>
        );
    }

    render()
    {
        return (
            <div className="c_tokenList">
                {this.renderMenu()}
            </div>
        );
    }
}

TokenList.defaultProps = {
    t: () => {},
};

TokenList.propTypes = {
    tradeList: PropTypes.object.isRequired,
    select: PropTypes.func.isRequired,
    t: PropTypes.func,
    tokenList: PropTypes.array.isRequired,
};

export default translate()(TokenList);
