import { ofType } from 'redux-observable';
import { of } from 'rxjs';
import { switchMap, map, mergeMap, catchError } from 'rxjs/operators';
import * as types from '../constants/actionTypesUser';
import {
    userUpdate,
    accountGet$,
    accountUpdate,
    myBooksGet$,
    myBooksUpdate,
    historyUpdate
} from '../actions/user';
import {
    bnHex,
    sellAmount,
    buyAmount,
    sellDisplayPrice,
    buyDisplayPrice,
    subHex,
    bnTen,
    tenFill
} from '../utils/format';
import * as rpc from '../utils/rpc';

// const getBalance = (address) => {

// }
const userGetEpic = (action$) =>
{
    return action$
        .pipe(
            ofType(types.USER_GET$),
            mergeMap(action =>
                of(
                    userUpdate(action.data),
                    accountGet$(),
                    myBooksGet$()
                ))
        );
};

const accountGetEpic = (action$, state$) =>
{
    return action$
        .pipe(
            ofType(types.ACCOUNT_GET$),
            // map((action) =>
            // {
            //     userUpdate(action.data);
            // }),
            // interval(15000),
            switchMap(() =>
            {
                const { tokenList } = state$.value.book;
                const { wallet } = state$.value.user.user;
                return rpc.getUserBalance(tokenList, wallet.address);
            }),
            map((res) =>
            {
                const arr = [];
                const { tokenList } = state$.value.book;

                res.forEach((r, i) =>
                {
                    // actions.userActions.getBalance(i, balance);
                    arr.push({
                        token: tokenList[i].token,
                        usable: r,
                        address: tokenList[i].address
                    });
                });
                return accountUpdate(arr);
            })
        );
};

const myBooksGetEpic = (action$, state$) =>
{
    return action$
        .pipe(
            ofType(types.MYBOOKS_GET$),
            switchMap((action) =>
            {
                if (!action.data)
                {
                    return window.dbObj
                        .ref('orderBooks')
                        .orderByChild('user')
                        .equalTo(`0x${state$.value.user.user.wallet.address}`)
                        .once('value');
                }
                else
                {
                    return of(action.data);
                }
            }),
            map((snapshot) =>
            {
                const { from, to, tokenList } = state$.value.book;
                const { user } = state$.value.user;
                const fromToken = tokenList.find(tk => tk.address === from.address);
                const toToken = tokenList.find(tk => tk.address === to.address);
                const data = Object.values(snapshot.val())
                    .filter(d => d.user === `0x${user.wallet.address}`)
                    .filter(d => (
                        (d.fromToken === from.address && d.toToken === to.address) ||
                        (d.fromToken === to.address && d.toToken === from.address)) &&
                        d !== undefined &&
                        bnHex(d.fromAmount).sub(bnHex(d.filled)).gt(bnTen((10 ** (from.decimal - 6)).toString()))
                    )
                    .sort((a, b) => (bnHex(a.price).toString() > bnHex(b.price).toString() ? 1 : -1)) // 依照價格排序
                    .sort((a, b) => (a.fromToken > b.fromToken ? 1 : -1)) // 依照名字排序
                    // .reduce((prev, curr) =>
                    // {
                    //     // 將價格相同的數量合併
                    //     if (prev.length <= 0 || prev[prev.length - 1].price !== curr.price)
                    //     {
                    //         prev.push(curr);
                    //     }
                    //     else
                    //     {
                    //         prev[prev.length - 1].fromAmount = bnHex(prev[prev.length - 1].fromAmount).add(bnHex(curr.fromAmount)).toString(16);
                    //         prev[prev.length - 1].filled = bnHex(prev[prev.length - 1].filled).add(bnHex(curr.filled)).toString(16);
                    //     }

                    //     return prev;
                    // }, [])
                    .map((v) =>
                    {
                        let book;
                        if (v.fromToken === from.address && v.toToken === to.address)
                        {
                            book = {
                                from: fromToken.token,
                                to: toToken.token,
                                desc: `${fromToken.token}/${toToken.token}`,
                                type: 'sell',
                                timestamp: v.timestamp,
                                price: v.price,
                                displayPrice: sellDisplayPrice(v.price, from.decimal, to.decimal),
                                amount: v.fromAmount,
                                displayAmount: tenFill(bnHex(v.fromAmount).toString(), -fromToken.decimal),
                                fill: v.filled
                            };
                        }
                        else if (v.fromToken === to.address && v.toToken === from.address)
                        {
                            book = {
                                from: toToken.token,
                                to: fromToken.token,
                                desc: `${fromToken.token}/${toToken.token}`,
                                type: 'buy',
                                timestamp: v.timestamp,
                                price: v.price,
                                displayPrice: buyDisplayPrice(v.price, from.decimal, to.decimal),
                                amount: v.fromAmount,
                                displayAmount: tenFill(bnHex(v.fromAmount).mul(bnHex(v.price)).toString(), -from.decimal - 18),
                                fill: bnHex(v.filled).mul(bnHex(v.price)).div(bnTen((10 ** 18).toString())).toString(16)
                            };
                        }
                        book.value = Number(book.amount);
                        return book;
                    })
                    .filter(v => !!v)
                    .sort((a, b) => b.timestamp - a.timestamp);

                data.forEach((d, i) =>
                {
                    d.no = i;
                    d.cancel = false;
                });

                return myBooksUpdate(data);
            }),
            catchError((err, obs) => obs)
        );
};

const historyGetEpic = (action$, state$) =>
{
    return action$
        .pipe(
            ofType(types.HISTORY_GET$),
            map((action) =>
            {
                const { from, to, tokenList } = state$.value.book;
                const fromToken = tokenList.find(v => v.address === from.address);
                const toToken = tokenList.find(v => v.address === to.address);
                const data = Object.values(action.data)
                    .filter(v => (v.fromToken === from.address && v.toToken === to.address) || (v.fromToken === to.address && v.toToken === from.address))
                    .sort((a, b) => (a.timestamp < b.timestamp ? 1 : -1))
                    .filter(v => v.type !== 'take')
                    .map((v) =>
                    {
                        let book;
                        if (v.fromToken === from.address && v.toToken === to.address)
                        {
                            book = {
                                no: v.no,
                                from: fromToken.token,
                                to: toToken.token,
                                desc: `${fromToken.token}/${toToken.token}`,
                                price: v.price,
                                displayPrice: sellDisplayPrice(v.price, from.decimal, to.decimal),
                                amount: sellAmount(v.fromAmount, v.filled, fromToken.decimal),
                                type: v.type,
                                timestamp: v.timestamp
                            };
                            if (v.type === 'cancel')
                            {
                                book.type = 'csell';
                            }
                            else if (v.type === 'trade')
                            {
                                book.type = 'tsell';
                            }
                        }
                        else if (v.fromToken === to.address && v.toToken === from.address)
                        {
                            book = {
                                no: v.no,
                                from: toToken.token,
                                to: fromToken.token,
                                desc: `${fromToken.token}/${toToken.token}`,
                                price: v.price,
                                displayPrice: buyDisplayPrice(v.price, from.decimal, to.decimal),
                                amount: buyAmount(v.fromAmount, v.filled, v.price, from.decimal, to.decimal),
                                type: v.type,
                                timestamp: v.timestamp
                            };
                            if (book.type === 'cancel')
                            {
                                book.type = 'cbuy';
                            }
                            else if (book.type === 'trade')
                            {
                                book.type = 'tbuy';
                            }
                        }
                        return book;
                    })
                    .filter(v => !!v);

                return historyUpdate(data);
            }),
            catchError((err, obs) =>
            {
                console.warn(err);
                return obs;
            })
        );
};

export default [
    userGetEpic,
    accountGetEpic,
    myBooksGetEpic,
    historyGetEpic
];
