import update from 'immutability-helper';
import * as types from '../constants/actionTypesModal';

const initialState = {
    loading: false,
    message: ''
};

export default function modal(state = initialState, action = {})
{
    switch (action.type)
    {
        case types.UPDATE_LOADING:
            return update(state, {
                loading: { $set: action.data }
            });
        case types.UPDATE_MESSAGE:
            return update(state, {
                message: { $set: action.data }
            });
        default:
            return state;
    }
}
