import React from 'react';
import { HashRouter, Switch, Route, Redirect } from 'react-router-dom';
import Main from '../components/main/main';
import asyncComponent from '../utils/asyncComponent';

export default function createRoutes()
{
    return (
        <HashRouter>
            <Main>
                <Switch>
                    <Route exact path="/" component={asyncComponent(() => import('../containers/landingPage/landingPage').then(module => module.default))} />
                    <Route exact path="/order" component={asyncComponent(() => import('../containers/order/order').then(module => module.default))} />
                    <Route exact path="/exchange" component={asyncComponent(() => import('../containers/index/index').then(module => module.default))} />
                    <Route exact path="/mortgage" component={asyncComponent(() => import('../containers/mortgage/mortgage').then(module => module.default))} />
                    <Route exact path="/offset" component={asyncComponent(() => import('../containers/offset/offset').then(module => module.default))} />
                    <Route exact path="/privacy" component={asyncComponent(() => import('../containers/privacy/privacy').then(module => module.default))} />
                    <Route exact path="/currency" component={asyncComponent(() => import('../containers/currency/currency').then(module => module.default))} />
                    {/* <Route exact path="/help" component={asyncComponent(() => import('../containers/help/help').then(module => module.default))} /> */}
                    <Route exact nomatch render={() => <Redirect to="/exchange" />} />
                </Switch>
            </Main>
        </HashRouter>
    );
}
