/* eslint-disable no-undef */
import React from 'react';
import update from 'immutability-helper';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';

// import Language from '../language/language';
import { language } from '../../constants/languageType';
import i18n from '../../i18n/i18n';
import { formatNumber, bnHex, tenFill } from '../../utils/format';
import TokenList from '../tokenList/tokenList';

import './header.scss';

@withRouter
class Header extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            lang: localStorage.getItem('i18nextLng'),
            menu: ['exchange', 'mortgage', 'currency'],
            showMenu: false,
            showList: false,
            showContract: false
        };
        this.changeHeader = this.changeHeader.bind(this);
        this.renderMenu = this.renderMenu.bind(this);
        this.renderLanguage = this.renderLanguage.bind(this);
        this.renderSummary = this.renderSummary.bind(this);
        this.renderContract = this.renderContract.bind(this);
        this.select = this.select.bind(this);
        this.closeList = this.closeList.bind(this);
    }

    // addToken()
    // {
    //     if (!this.state.add)
    //     {
    //         this.setState(update(this.state, {
    //             add: { $set: true }
    //         }));
    //     }
    //     else
    //     {
    //         if (this.state.new_token === '') return console.log('not add');
    //         this.props.addTokenIO(this.state.new_token);
    //         Io.emit('newToken', { address: this.state.new_token });
    //         this.setState(update(this.state, {
    //             add: { $set: false },
    //             modal: { $set: true },
    //         }));
    //         this.address.value = '';

    //         setTimeout(() =>
    //         {
    //             this.setState(update(this.state, {
    //                 modal: { $set: false },
    //                 new_token: { $set: '' }
    //             }));
    //         }, 3000);
    //     }
    // }

    // enterToken(e)
    // {
    //     this.setState(update(this.state, {
    //         new_token: { $set: e.target.value }
    //     }));
    // }

    componentDidMount()
    {
        if (this.props.dash)
        {
            window.addEventListener('click', this.closeList);
        }
    }

    shouldComponentUpdate(nextProp, nextState)
    {
        if (this.props.dash || this.state.showMenu !== nextState.showMenu || this.state.lang !== nextState.lang)
        {
            return true;
        }
        else
        {
            return false;
        }
    }

    componentWillUnmount()
    {
        if (this.props.dash)
        {
            window.removeEventListener('click', this.closeList);
        }
    }

    changeHeader(lan)
    {
        const tar = language.find(l => l.type === lan);
        i18n.changeLanguage(tar.name);
        this.setState(update(this.state, {
            lang: { $set: localStorage.getItem('i18nextLng') }
        }));
    }

    closeList(e)
    {
        if ((this.list && !this.list.contains(e.target)) && !this.desc.contains(e.target))
        {
            this.setState(update(this.state, {
                showList: { $set: false }
            }));
        }

        // if (!this.contractBtn.contains(e.target) && !this.contract.contains(e.target))
        // {
        //     this.setState(update(this.state, {
        //         showContract: { $set: false }
        //     }));
        // }
    }

    select(obj)
    {
        const { myTransaction, select, actions } = this.props;
        select(obj);
        this.setState(update(this.state, {
            showList: { $set: false }
        }));
        actions.userActions.myTransactionChange({
            price: '',
            amount: '',
            able: false,
            total: '-',
            act: myTransaction.act
        });
    }

    renderContract()
    {
        const { t } = this.props;

        return (
            <div
                className="contract"
                ref={(el) => { this.contractBtn = el; }}
                onMouseEnter={() => { this.setState(update(this.state, { showContract: { $set: true } })); }}
                onMouseLeave={() => { this.setState(update(this.state, { showContract: { $set: false } })); }}
            >
                <i className="fa fa-file-text-o" aria-hidden="true" />
                <p>{t('header.contract')}</p>
                <i className="fa fa-chevron-down" aria-hidden="true" />
                <div className={this.state.showContract ? 'open' : ''} ref={(el) => { this.contract = el; }}>
                    <div>
                        <span>Etherscan {t('header.contract')}</span>
                    </div>
                    <div>
                        <a className="link" target="_blank" href="https://etherscan.io/address/0x8ea74569c1b9bbb13780114b6b5e93396910070a">
                            <div>{finalConfig.xpaExchange}</div>
                            <div>— the current smart contart</div>
                        </a>
                    </div>
                </div>
            </div>
        );
    }

    renderLanguage()
    {
        return language.map((item) =>
        {
            return (
                <div key={item.name} className={this.state.lang === item.name ? 'active' : ''} onClick={() => { this.changeHeader(item.type); }}>
                    <a>{item.type}</a>
                    {this.state.lang === item.name ? <i className="fa fa-check" aria-hidden="true" /> : null}
                </div>
            );
        });
    }

    renderList()
    {
        const { tradeList, actions, tokenList } = this.props;
        return (
            <div className={this.state.showList ? 'token show' : 'token'} ref={(el) => { this.list = el; }}>
                <TokenList
                    tradeList={tradeList}
                    select={this.select}
                    actions={actions}
                    tokenList={tokenList}
                />
            </div>
        );
    }

    renderMenu()
    {
        const { t } = this.props;
        return this.state.menu.map((item) =>
        {
            return (
                <div key={item} className={this.props.match.path.replace('/', '') === item ? 'active' : ''}>
                    <Link to={item}>
                        {t(`header.${item}`)}
                    </Link>
                </div>
            );
        });
    }

    renderSummary()
    {
        const { from, to, tradeList, t } = this.props;
        if (Object.keys(tradeList) < 1) return null;
        const summary = tradeList[to.token] ? tradeList[to.token].find(d => d.address === from.address) : {
            latest: 0,
            growth: 0,
            max: 0,
            min: 0,
            amount: 0
        };

        return (
            <div className="summary">
                {this.renderList()}
                <div className="desc" ref={(el) => { this.desc = el; }} onClick={() => { this.setState(update(this.state, { showList: { $set: !this.state.showList }, showMenu: { $set: false } })); }}>
                    <div>
                        <span>{from.token}/{to.token}</span>
                        <span>{t('header.chooseToken')}</span>
                    </div>
                    <i className="fa fa-chevron-down" aria-hidden="true" />
                </div>
                <div className="rate">
                    <span>
                        {formatNumber(8, tenFill(bnHex(summary.latest).toString(), from.decimal - 18 - to.decimal))}
                    </span>
                    <span className={summary.growth >= 0 ? 'up' : 'down'}>
                        {formatNumber(4, summary.growth)}%
                    </span>
                </div>
                <div className="infor">
                    <div>
                        <p>
                            {t('header.24hrHigh')}
                        </p>
                        <span>
                            {formatNumber(8, tenFill(bnHex(summary.max).toString(), from.decimal - 18 - to.decimal)) || 0}
                        </span>
                    </div>
                    <div>
                        <p>
                            {t('header.24hrLow')}
                        </p>
                        <span>
                            {formatNumber(8, tenFill(bnHex(summary.min).toString(), from.decimal - 18 - to.decimal)) || 0}
                        </span>
                    </div>
                    <div>
                        <p>
                            {t('header.24hrVolume')}
                        </p>
                        <span>
                            {formatNumber(8, tenFill(bnHex(summary.amount).toString(), -from.decimal))} {from.token}
                        </span>
                    </div>
                </div>
            </div>
        );
    }

    render()
    {
        const { lang } = this.state;
        const { t } = this.props;

        const production = finalConfig.production;

        const guide = () =>
        {
            switch (lang)
            {
                case 'zh-cn':
                    return 'https://www.weibo.com/ttarticle/p/show?id=2309404226836979686902';
                case 'zh-tw':
                    return 'https://medium.com/@xpa_76300/xpa-exchange-%E6%96%B0%E6%89%8B%E4%B8%8A%E8%B7%AF-e85247bae3b';
                default:
                    return 'https://medium.com/tideisun-blog/xpa-exchange-a-gif-guide-to-making-a-trade-4eda6f7c3c76';
            }
        };

        return (
            <header className="c_header">
                <div className={this.state.showList || this.state.showContract ? 'header-desktop block' : 'header-desktop'}>
                    {!production ? <img className="beta" src="/img/Beta-Ribbon.png" alt="beta" /> : ''}
                    <h1>
                        <Link to="/">
                            <div className="logo"><img src="/img/logo_xpaex.png" alt="xpa" /></div>
                            XPA<span className="small-title">Exchange</span>
                        </Link>
                        {/* <span className="chain">Contract: {finalConfig.xpaExchange}</span> */}
                    </h1>
                    {/* <div className="controls">
                        <a
                            href={guide()}
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            <i className="fa fa-bookmark-o" aria-hidden="true" style={{ fontSize: '16px', paddingBottom: '3px' }} />
                            <span>{t('header.guide')}</span>
                        </a>
                    </div>

                    {!production ?
                        <div className="controls">
                            <Link to="mortgage">
                                <i className="fa fa-university" />
                                <span>{t('header.mortgage')}</span>
                            </Link>
                        </div> : ''}
                    <Language changeHeader={this.changeHeader} /> */}
                    {this.props.dash ? this.renderSummary() : null }
                    <div className="empty" />
                    {this.props.dash ? this.renderContract() : null }
                    <div className="menu" onClick={() => { this.setState(update(this.state, { showMenu: { $set: true }, showList: { $set: false } })); }}>
                        <span />
                        <span />
                        <span />
                    </div>
                    <div className={this.state.showMenu ? 'cover show' : 'cover'} onClick={() => { this.setState(update(this.state, { showMenu: { $set: false } })); }} />
                    <div className={this.state.showMenu ? 'control show' : 'control'}>
                        <div className="group">
                            {this.renderMenu()}
                        </div>
                        <div className="group">
                            {this.renderLanguage()}
                        </div>
                        <div>
                            <div>
                                <a href={guide()} target="_blank">{t('header.help')}</a>
                            </div>
                        </div>
                        <div className="version">
                            <div>
                                <a>v1.5.2</a>
                            </div>
                        </div>
                    </div>
                </div>
            </header>
        );
    }
}

Header.defaultProps = {
    t: () => {},
    match: {},
    from: {},
    to: {},
    dash: false,
    tradeList: {},
    tokenList: [],
    actions: {},
    select: () => {},
    myTransaction: {}
};

Header.propTypes = {
    t: PropTypes.func,
    match: PropTypes.object,
    from: PropTypes.object,
    to: PropTypes.object,
    dash: PropTypes.bool,
    tradeList: PropTypes.object,
    tokenList: PropTypes.array,
    actions: PropTypes.object,
    select: PropTypes.func,
    myTransaction: PropTypes.object,
};

export default translate()(Header);
