import i18n from 'i18next';
import XHR from 'i18next-xhr-backend';
import LanguageDetector from 'i18next-browser-languagedetector';

const options = {
    // order and from where user language should be detected
    order: ['querystring', 'cookie', 'localStorage', 'navigator', 'htmlTag', 'path', 'subdomain'],

    // keys or params to lookup language from
    lookupQuerystring: 'lng',
    lookupCookie: 'i18next',
    lookupLocalStorage: 'i18nextLng',
    lookupFromPathIndex: 0,
    lookupFromSubdomainIndex: 0,

    // cache user language on
    caches: ['localStorage', 'cookie'],
    excludeCacheFor: ['cimode'], // languages to not persist (cookie, localStorage)

    // optional expire and domain for set cookie
    cookieMinutes: 10,
    cookieDomain: 'myDomain',

    // optional htmlTag with lang attribute, the default is:
    htmlTag: document.documentElement
};

const lngDetector = new LanguageDetector(null, options);

i18n.use(XHR)
    .use(lngDetector)
    .init({
        whitelist: [
            'en-us', 'zh-tw', 'zh-cn', 'ko-kr', 'ja-jp'
        ],

        fallbackLng: 'en-us', // 未偵測到時的後備語系

        ns: ['common'], // 語系的 loading namespace 如語系檔案名稱 common.js

        defaultNS: 'common', // 預設的 namespace name

        debug: false,

        interpolation: {
            escapeValue: false // not needed for react!!
        },

        load: 'currentOnly',

        lowerCaseLng: true,

        backend: {
            // 設定語系檔案的 server 路徑, 會以GET的方式跟 server 要檔案
            // lng = 語系代碼 ns = namespace
            loadPath: '/locales/{{lng}}/{{ns}}.json'
        },

        detection: {
            lookupLocalStorage: 'i18nextLng'
        },

    });

export default i18n;
