/* eslint-disable no-undef */
import update from 'immutability-helper';
import * as types from '../constants/actionTypesBook';

const initialState = {
    from: { token: 'XPA', address: finalConfig.XPA, decimal: 18 },
    to: { token: 'ETH', address: finalConfig.ethAddress, decimal: 18 },
    buy: [],
    sell: [],
    tokenList: [],
    summary: {},
    tradeList: {},
    transaction: [],
    kLine: [],
    timeunit: 60 * 60 * 1000
};

export default function book(state = initialState, action = {})
{
    switch (action.type)
    {
        case types.FROM_CHANGE:
            return update(state, {
                from: { $set: action.data }
            });
        case types.TO_CHANGE:
            return update(state, {
                to: { $set: action.data }
            });
        case types.BOOK_UPDATE:
            return update(state, {
                sell: { $set: action.data.sell },
                buy: { $set: action.data.buy }
            });
        case types.TOKENLIST_GET:
            return update(state, {
                tokenList: { $set: action.data }
            });
        case types.SUMMARY_UPDATE:
            return update(state, {
                summary: { $set: action.data }
            });
        case types.TRADELIST_GET:
            return update(state, {
                tradeList: { $set: action.data }
            });
        // case types.ADD_TRADELIST:
        //     return update(state, {
        //         tradeList: {
        //             star: { $set: [action.data] }
        //         }
        //     });
        case types.RECENTTRANSACTION_UPDATE:
            return update(state, {
                transaction: { $set: action.data }
            });
        case types.KLINE_UPDATE:
            return update(state, {
                kLine: { $set: action.data }
            });
        case types.TIMEUINT_CHANGE:
            return update(state, {
                timeunit: { $set: action.data }
            });
        default:
            return state;
    }
}
